<script>
    import { Router, Link } from "svelte-navigator";
    import { _ } from "svelte-i18n";
    import { uuid2name } from "./common";

    export let title,
        items,
        sub,
        translateAsUuid = null;

    let y;
    let menu;
    let elements;
    let search;

    function searchChange() {
        elements = document.querySelectorAll("[data-id]");
        elements.forEach((element) => {
            let translated = element.attributes["data-translated"].value;
            translated = translated.toLowerCase();

            let id = element.attributes["data-id"].value;
            id = id.toLowerCase();

            if (
                search == "" ||
                translated.includes(search) ||
                id.includes(search)
            ) {
                element.classList.remove("hide");
            } else {
                element.classList.add("hide");
            }
        });
    }

    function getProps({ isCurrent }) {
        return isCurrent ? { class: "active" } : {};
    }

    function translated(s) {
        return translateAsUuid == null
            ? $_("minecraft." + sub + "." + s)
            : uuid2name(s);
    }
</script>

<div
    class="side-menu"
    bind:this={menu}
    on:scroll={(e) => (y = e.target.scrollTop)}
    on:click={(menu.scrollTop = y)}
>
    <h1>{title}</h1>
    {#await items}
        <div class="loading">{$_("general.loading")}</div>
    {:then results}
        <input
            type="text"
            name="search_input"
            placeholder="Filter ..."
            bind:value={search}
            on:input={searchChange}
        />

        <Router>
            {#each results as result}
                <div>
                    <Link
                        to={result}
                        {getProps}
                        data-id={result}
                        data-translated={translated(result)}
                    >
                        {translated(result)}
                    </Link>
                </div>
            {/each}
        </Router>
    {/await}
</div>

<style type="scss">
    .side-menu {
        grid-area: "SideMenu";
        overflow-y: scroll;
        height: 100%;
        border: 1px solid var(--black-line);
        border-top: none;
        border-bottom: none;
        box-shadow: 3px 0 7px -2px var(--black-line);
        padding: 0 1vw 0 1vw;

        h1 {
            border-bottom: 2px solid var(--highlight);
        }

        input {
            display: block;
            border: 2px solid var(--highlight);
            line-height: 2.5em;
            border-radius: 3px;
            padding: 0 0.5vw;
        }

        input:focus {
            border-color: var(--highlight-table);
        }

        :global(a) {
            line-height: 2.5em;
        }

        :global(a.active) {
            border-bottom: 2px solid var(--highlight-table);
        }

        :global(a:hover) {
            border-bottom: 2px solid var(--highlight);
        }

        h1 {
            margin: 1vh 0;
        }
    }
</style>
