import { _ } from "svelte-i18n";

/* TRANSLATION */
export function getTranslation(sub, name) {
    return _("minecraft." + sub + "." + name);
}

/* UUID */
export let uuids = fetch("/data/uuid2name.json")
    .then(response => response.json())
    .then((data) => uuids = data);

export function uuid2name(u) {
    return (u in uuids) ? uuids[u].name : "unknown"
}

/* STATISTIC */
export function getStatistic(s) {
    return fetch("/data/" + s + ".json").then((res) => res.json());
}