<script>
	import { _ } from "svelte-i18n";
	import { getStatistic } from "./common";
	import StatsTable from "./StatsTable.svelte";
	import SideMenu from "./SideMenu.svelte";

	let items = getStatistic("stat");
	let title = "Highscores";
	let sub = "stat";
	let contentWindow;

	export let el;
</script>

<SideMenu {sub} {title} {items} />
<div class="highscores" bind:this={contentWindow}>
	<div class="content">
		<div class="stats_grid">
			{#await getStatistic("stat") then result}
				{#each result as title}
					{#await getStatistic(sub + "/" + title) then stats}
						<StatsTable
							{sub}
							{title}
							statistics={stats.custom}
							{el}
							{contentWindow}
						/>
					{/await}
				{/each}
			{/await}
		</div>
	</div>
</div>

<style type="scss">
	.highscores {
		grid-area: "MainContent";
		overflow-y: scroll;
	}

	.content {
		padding: 1vh 1vw;
	}
</style>
