<script>
	import { Router, Link } from "svelte-navigator";
	import { uuid2name } from "./common";
	import { _ } from "svelte-i18n";

	export let title;
	export let statistics;
	export let contentWindow;
	export let el;

	let element;

	$: if (el) {
		try {
			if (element && element != null) {
				element.classList.remove("glow");
			}

			let header = document.getElementsByTagName("header")[0];
			element = document.querySelector(`[data-element="${el}"]`);

			if (element && element != null) {
				element.classList.add("glow");
				contentWindow.scrollTop =
					element.offsetTop - header.offsetHeight * 2;
			}
		} catch (e) {
			console.log(e);
		}
	}
</script>

<Router>
	<div class="stats_wrapper">
		<h6 data-element={title}>{$_("minecraft.stat." + title)}</h6>
		<div class="table">
			<div class="header">
				<span>{$_("general.table_header.playername")}</span>
			</div>
			<div class="header count">
				<span>{$_("general.table_header.count")}</span>
			</div>
			{#each Object.entries(statistics).reverse() as [count, uuids], i}
				<div class="players {i % 2 === 0 ? 'even' : ''}">
					{#each uuids as uuid}
						{#await uuid2name(uuid) then u}
							<div>
								<img
									src="https://mc-heads.net/avatar/{uuid}/32"
									alt="Avatar {u}"
								/>
								<Link to="/players/{uuid}" class="player_link"
									>{u}</Link
								>
							</div>
						{/await}
					{/each}
				</div>
				<div class="count {i % 2 === 0 ? 'even' : ''}">{count}</div>
			{/each}
		</div>
	</div>
</Router>

<style type="scss">
	@keyframes pulse {
		0% {
			border-color: var(--highlight);
			background: none;
		}

		70% {
			border-color: var(--highlight-table);
			background: var(--highlight-table);
		}

		100% {
			border-color: var(--highlight);
			background: none;
		}
	}

	.stats_wrapper {
		margin-top: 0.5em;

		h6 {
			border-bottom: 2px solid var(--highlight);
		}
	}

	:global(.glow) {
		border-color: var(--highlight-table);
		animation: pulse 0.75s 3;
	}

	:global(.player_link:hover) {
		border-bottom: 2px solid var(--highlight);
	}

	.table {
		display: grid;
		grid-template-columns: 1fr auto;
		background: var(--darkest);

		.even {
			background: var(--darker);
		}

		.header {
			padding: 0.5em 0.5em;
			font-size: 0.8em;
			color: var(--highlight);
		}

		.players {
			div {
				img {
					vertical-align: middle;
					padding-bottom: 0.4em;
				}

				img:first-of-type {
					padding-top: 0.4em;
				}
			}
		}

		.count {
			display: flex;
			justify-content: right;
			align-items: center;
			font-family: "Source Code Pro", monospace;
		}

		.players,
		.count {
			padding: 0 0.5em;
		}

		.players:nth-last-child(-n + 2),
		.count:nth-last-child(-n + 2) {
			border-bottom: none;
		}
	}
</style>
